
<template>
  <div class="box">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="企业信息" name="first">
        <div class="container" >
                企业名称：
                <el-input class="com-input"
                  placeholder="请输入内容"
                  v-model="comName">
                </el-input>
                修改人：
                <el-input class="com-input"
                  placeholder="请输入内容"
                  v-model="comName">
                </el-input>
                状态：
                <el-select v-model="value" placeholder="全部">
                  <el-option
                    v-for="item in checkStatus"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
                <el-button type="primary" icon="el-icon-search" class="btn">查询</el-button>
                <el-main>
                  <el-table :data="comInfoList">
                    <el-table-column prop="id" label="ID">
                    </el-table-column>
                    <el-table-column prop="comName" label="企业名字">
                    </el-table-column>
                    <el-table-column prop="address" label="企业地址">
                    </el-table-column>
                    <el-table-column prop="property" label="修改人">
                    </el-table-column>
                    <el-table-column prop="type" label="修改时间" width="200">
                    </el-table-column>
                    <el-table-column prop="isQuoted" label="修改原因">
                    </el-table-column>
                    <el-table-column prop="phone" label="状态">
                    </el-table-column>
                    <el-table-column label="操作" width='220'>
                      <template slot-scope="scope">
                        <el-button @click="handleClick2(scope.row)" type="text" size="small">查看详情</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                  <div class="demonstration-box">
                    <!-- <span class="demonstration">显示总数</span> -->
                    <el-pagination
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :current-page.sync="currentPage"
                      :page-size="100"
                      layout="total, prev, pager, next"
                      :total="1000">
                    </el-pagination>
                  </div>
                </el-main>
        </div>
      </el-tab-pane>
      <el-tab-pane label="商品信息" name="second">
        <div class="container" >
                商品名称：
                <el-input class="com-input"
                  placeholder="请输入内容"
                  v-model="comName">
                </el-input>
                修改人：
                <el-input class="com-input"
                  placeholder="请输入内容"
                  v-model="comName">
                </el-input>
                状态：
                <el-select v-model="value" placeholder="全部">
                  <el-option
                    v-for="item in checkStatus"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
                <el-button type="primary" icon="el-icon-search" class="btn">查询</el-button>
                <el-main>
                  <el-table :data="goodsInfoList">
                    <el-table-column prop="id" label="ID">
                    </el-table-column>
                    <el-table-column prop="comName" label="商品名称">
                    </el-table-column>
                    <el-table-column prop="address" label="所属计划">
                    </el-table-column>
                    <el-table-column prop="property" label="修改人">
                    </el-table-column>
                    <el-table-column prop="type" label="修改时间">
                    </el-table-column>
                    <el-table-column prop="isQuoted" label="修改原因">
                    </el-table-column>
                    <el-table-column prop="phone" label="类型">
                    </el-table-column>
                    <el-table-column prop="status" label="状态">
                    </el-table-column>
                    <el-table-column label="操作" width='220'>
                      <template slot-scope="scope">
                        <el-button @click="handleClick3(scope.row)" type="text" size="small">查看详情</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                  <div class="demonstration-box">
                    <!-- <span class="demonstration">显示总数</span> -->
                    <el-pagination
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :current-page.sync="currentPage"
                      :page-size="100"
                      layout="total, prev, pager, next"
                      :total="1000">
                    </el-pagination>
                  </div>
                </el-main>
        </div>
      </el-tab-pane>
      <el-tab-pane label="服务单信息" name="third">
        <div class="container" >
               <div>
                  被服务人名称：
                  <el-input class="com-input"
                    placeholder="请输入内容"
                    v-model="comName">
                  </el-input>
                  被服务人手机号：
                  <el-input class="com-input"
                    placeholder="请输入内容"
                    v-model="comName">
                  </el-input>
                </div> 
                <div style="margin-top: 10px;">
                  修改人：
                  <el-input class="com-input"
                    placeholder="请输入内容"
                    v-model="comName">
                  </el-input>
                  状态：
                  <el-select v-model="value" placeholder="全部">
                    <el-option
                      v-for="item in checkStatus"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                  <el-button type="primary" icon="el-icon-search" class="btn">查询</el-button>
                </div>
                
                <el-main>
                  <el-table :data="serveInfoList">
                    <el-table-column prop="id" label="ID">
                    </el-table-column>
                    <el-table-column prop="comName" label="被服务人姓名">
                    </el-table-column>
                    <el-table-column prop="address" label="被服务人手机号">
                    </el-table-column>
                    <el-table-column prop="property" label="修改人">
                    </el-table-column>
                    <el-table-column prop="type" label="修改时间">
                    </el-table-column>
                    <el-table-column prop="isQuoted" label="修改原因">
                    </el-table-column>
                    <el-table-column prop="phone" label="类型">
                    </el-table-column>
                    <el-table-column prop="status" label="状态">
                    </el-table-column>
                    <el-table-column label="操作" width='220'>
                      <template slot-scope="scope">
                        <el-button @click="handleClick4(scope.row)" type="text" size="small">查看详情</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                  <div class="demonstration-box">
                    <!-- <span class="demonstration">显示总数</span> -->
                    <el-pagination
                      @size-change="handleSizeChange"
                      @current-change="handleCurrentChange"
                      :current-page.sync="currentPage"
                      :page-size="100"
                      layout="total, prev, pager, next"
                      :total="1000">
                    </el-pagination>
                  </div>
                </el-main>
        </div>
      </el-tab-pane>
  </el-tabs>
  </div>
</template>

<script>
import {checkStatus, comInfo, goodsInfo, serveInfo} from '../../../data/recheck.js'
export default {
  name: 'infoRecheck',
  data(){
    return {
      activeName: 'first',
      checkStatus,
      value: '',
      comName: '',
      comInfoList: Array(10).fill(comInfo),
      goodsInfoList: goodsInfo,
      serveInfoList: serveInfo,
      currentPage: 1
    }
  },
  methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },
      handleClick2(tab, event) {
        this.$router.push('comInfo')
        console.log(tab, event);
      },
      handleClick3(tab, event) {
        let status = tab.phone === '下架' ? '1' : '0' 
        this.$router.push('goodsInfo?status=' + status)
        console.log(tab, event);
      },
      handleClick4(tab, event) {
        let status = tab.phone === '删除' ? '1' : '0' 
        this.$router.push('serveInfo?status=' + status)
        console.log(tab, event);
      },
      handleSizeChange(val) {
          console.log(`每页 ${val} 条`);
        },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      }
    }
}
</script>

<style lang="less" scoped>
  .box{
    padding-left: 10px;
  }
  .container{
    padding-bottom: 10px;
    border-bottom: 1px solid #eee;
  }
  .com-input {
    width: 210px;
    margin-right: 20px
  }
  .btn{
    margin-left: 20px;
  }
</style>
